<template>
    <div>
        <v-toolbar color="primary" app
            style="color: #E8E8E8"
        >
            <v-btn icon
                @click.stop="drawer = !drawer"
            >
                <v-icon
                    color="#E8E8E8"
                >list</v-icon>
            </v-btn>

            <v-toolbar-title v-if="page === 'dashboard'">
                {{ user.property }}
            </v-toolbar-title>

            <v-toolbar-title v-if="page === 'user'">
                {{ currentUser.displayName }}
            </v-toolbar-title>

            <v-spacer></v-spacer>

            <v-btn icon v-if="page === 'dashboard'"
                to="user/devices"
            >
                <v-icon color="#E8E8E8"
                    class="mt-1"
                >
                    fas fa-tablet-rugged
                </v-icon>
            </v-btn>

            <v-btn icon>
                <v-icon class="mr-0 mt-1"
                    color="#E8E8E8"
                >
                    fas fa-calendar
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-2 mr-2"
            >
                {{ day }}
            </v-toolbar-title>

            <v-btn icon>
                <v-icon class="mr-0 mt-1"
                    color="#E8E8E8"
                >
                    fas fa-clock
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-2 mr-2"
            >
                {{ now }}
            </v-toolbar-title>
            </v-toolbar>
        <v-navigation-drawer
            v-model="drawer"
            temporary
            app
        >

            <v-list class="pa-1">
                <v-list-tile avatar>
                <v-list-tile-avatar>
                    <img :src="user.avatar">
                </v-list-tile-avatar>

                <v-list-tile-content>
                    <v-list-tile-title>
                    {{ currentUser.displayName }}
                    </v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>
            </v-list>

            <v-list class="pt-0" dense>
                <v-divider></v-divider>

                <v-list-tile
                    v-for="opt in options"
                    :key="opt.type"
                    :to="opt.link"
                >

                <v-list-tile-action>
                    <v-icon color="primary">{{ opt.icon }}</v-icon>
                </v-list-tile-action>

                <v-list-tile-content>
                    <v-list-tile-title>{{ opt.type }}</v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>

                <v-list-tile v-if="page === 'user'"
                    to="/dashboard"
                >

                    <v-list-tile-action>
                        <v-icon color="primary">fas fa-home</v-icon>
                    </v-list-tile-action>

                    <v-list-tile-content>
                        <v-list-tile-title>Back to Dashboard</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>


                <v-list-tile @click="logout()"
                >

                    <v-list-tile-action>
                        <v-icon color="primary">fas fa-sign-out-alt</v-icon>
                    </v-list-tile-action>

                    <v-list-tile-content>
                        <v-list-tile-title>Sign Out</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import firebase from 'firebase';

export default {
  name: 'Toolbar',
  props: {
    page: String,
  },
  computed: {
    ...mapState([
      'user',
    ]),
    ...mapGetters([
      'GET_DARK',
    ]),

    currentUser () {
        return firebase.auth().currentUser;
    }
  },
  data: () => ({
    options: [
      { type: 'My Profile', icon: 'fas fa-user-circle', link: '/user/profile' },
      { type: 'Devices', icon: 'fas fa-tablet-rugged', link: '/user/devices' },
      { type: 'Dashboard Settings', icon: 'fas fa-grip-horizontal', link: '/user/dashboard_settings' },
      { type: 'Property Settings', icon: 'fas fa-image', link: '/user/property' },
      { type: 'Privacy & Access Settings', icon: 'fas fa-lock-alt', link: '/user/privacy' },
      { type: 'Feedback', icon: 'fas fa-comment-alt-dots', link: '/user/feedback' },
    ],

    drawer: false,
    now: new Date(),
    day: new Date(),
  }),

  created() {
    setInterval(() => this.now = (new Date()).toLocaleTimeString('en-AU'));

    setInterval(() => this.day = (new Date()).toLocaleDateString('en-AU'));
  },

  methods: {
    ...mapActions([
      'DARK_MODE',
    ]),

    logout() {
        firebase.auth().signOut().then(() => {
            this.$router.replace('/');
        });
    }
  },
};

</script>
